import { URLExt } from '@jupyterlab/coreutils';
import { ServerConnection } from '@jupyterlab/services';
/**
 * Call the API extension
 *
 * @param endPoint API REST end point for the extension
 * @param init Initial values for the request
 * @returns The response body interpreted as JSON
 */
async function requestAPI(endPoint = '', init = {}) {
    // Make request to Jupyter API
    const settings = ServerConnection.makeSettings();
    const requestUrl = URLExt.join(settings.baseUrl, 'jupyterlab-genv', // API Namespace
    endPoint);
    let response;
    try {
        response = await ServerConnection.makeRequest(requestUrl, init, settings);
    }
    catch (error) {
        throw new ServerConnection.NetworkError(error);
    }
    let data = await response.text();
    if (data.length > 0) {
        try {
            data = JSON.parse(data);
        }
        catch (error) {
            console.log('Not a JSON response body.', response);
        }
    }
    if (!response.ok) {
        throw new ServerConnection.ResponseError(response, data.message || data);
    }
    return data;
}
export var Handler;
(function (Handler) {
    async function activate(kernel_id, eid) {
        const body = JSON.stringify({
            eid: eid,
            kernel_id: kernel_id
        });
        await requestAPI('activate', {
            body: body,
            method: 'POST'
        });
    }
    Handler.activate = activate;
    async function devices() {
        return await requestAPI('devices');
    }
    Handler.devices = devices;
    async function envs() {
        return await requestAPI('envs');
    }
    Handler.envs = envs;
    async function find(kernel_id) {
        return (await requestAPI(`find?kernel_id=${kernel_id}`)) || null;
    }
    Handler.find = find;
})(Handler || (Handler = {}));
